












































import ut from '../../utils/index.js';
import marked from '../../components/marked'
import wxParse from '../../components/mpvue-wxparse/src/wxParse.vue'
import comment from '../../components/comment.vue'
export default {
	data() {
		return {
			static:'',
			typeindex:0,
			typelist:[],
			serverinf:{},
			detailinf: '',
			comlist:[],
			pop:false
		}
	},
	components: {
		wxParse,
		comment
	},
	onLoad(opt) {
		this.static=ut.static;
		this.req_detail(opt._id);
		this.req_comment(opt._id)
		ut.settitle(opt.title||'服务详情');
	},
	methods: {
		cgpop(){
			this.cg_pop();
		},
		cg_pop(){
			if(!this.comlist.length){
				ut.totast('暂无评价');
				return
			}
			this.pop=!this.pop;
		},
		onShareAppMessage() {
			var that = this;
			return {
				title:'',
				success: (res) => {
					console.log("转发成功", res);
				},
				fail: (res) => {
					console.log("转发失败", res);
				}
			}
		},
		back_index_build(){
			wx.navigateTo({
				url: `../build/build2?source=1`
			})
		},
		cg_typeindex(index){
			this.typeindex=index;
			this.serverinf=this.typelist[index];
			const reg=new RegExp('/attach/download\\?filePath=','g');
			this.detailinf=marked(this.serverinf.detail.replace(reg,ut.static));
		},
		go_home_pay(){
			wx.setStorageSync('serverinf',this.serverinf)
			if(wx.getStorageSync('token')){
				wx.navigateTo({
					url: '../home/pay'
				})
			}else{
				wx.navigateTo({
					url: '../mine/login'
				})
			}
		},
		req_detail(id){
			ut.request({
				data: {
					classid:id
				},
				url: "service/detail"
			}).then(data=>{
				if(data[0]){
					this.serverinf=data[0]
					this.typelist=data;
					const reg=new RegExp('/attach/download\\?filePath=','g');
					this.detailinf=marked(data[0].detail.replace(reg,ut.static))
					console.log(this.detailinf)
				}else{
					ut.totast('服务信息不存在');
				}
			})
		},
		req_comment(id){
			ut.request({
				data: {
					proId:id,
					type:2
				},
				method:"GET",
				url: "comment/list"
			}).then(data=>{
				data.forEach(item=>{
					item.pictures =item.pictures.split(',')
				})
				this.comlist=data
			})
		}
	}
}
